.container {
  width: 100%;
  height: 400vh;
  position: relative;
  }

  

  .videoContainer {
    position: sticky;
    width: 100%;
    top: 0;
    height: 100vh;
  }

  .videoContainer__DotContainer{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    position: absolute;
    right: 30px;
    top: 50%;
    z-index: 99;
  }

  .videoContainer__DotContainer__dot{
    color: white;
    opacity: 0.2;
    font-size: 10px;
  }

  .videoContainer__DotContainer__dot.changeDot{
    opacity: 1;
    transition: 0.5s ease-in-out;
  }
  
  
  .video {
    width: 100%;
    object-fit: cover;
    height: 100%;
    position: absolute;
    opacity: 1;
    left: 0;
    right: 0;
    margin: 0 auto;
    /* display: none; */
  }

  .video.fadeOut{
    animation: fadeOut 0.5s;
    z-index: 99;
  }


  .zx-10{
    z-index: 10;
  }


  .zx-9{
    z-index: 9;
  }

  .zx-8{
    z-index: 8;
  }

  .zx-7{
    z-index: 7;
  }

  

