  .container__letterContainer {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
  }
  
  .longParagraph {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .longParagraph.scroll {
    /* overflow-y: scroll; */
  }
  
  .longParagraph__container__p{
    max-width: 500px;
    text-align: center;
    width: 36vw;
  }

  .longParagraph__container{
    margin: 60vh 0;
    display: flex;
    justify-content: center;
}



