@keyframes fadeOut {
  0% {
    opacity: 0.3;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0%   {opacity: 1;}
  50%  {opacity: 0.7;}
  100% {opacity: 1;}
}


