.container{
    /* max-height: 500px; */
    min-height: 225px;
    position: relative;
    box-sizing: border-box;
    padding: 5px;
    width: 100%;
    border-radius: 15px;
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
    cursor: pointer;
}


.container.hoverTransition:hover{
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
    /* 약간 떠오르는 효과 */
    transform: translateY(-5px);
}



