.container__img_container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1vw
}

.container__img_container__img{
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 15px;
}

.container__img_container__img.NoborderRadius{
    height: 450px;
    border-radius: 0px;
}



.container__padding__container{
    width: 100%;
    position: relative;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: start;
}


.container__padding__container__title{
    margin-bottom: 12px;
}

.container__padding__container__title p{
    font-size: 18px;
    font-weight: 500;
}

.container__padding__container__paragraph{
    color:#72818C;
    margin-bottom: 24px;
    line-height: 1.3;
}


.container__padding__container__yearDate{
    color:black;
    margin-bottom: 24px;
}

.container__padding__container__go{
    display: flex;
    width: 100%;
    cursor: pointer;
    padding: 5px 0px;
}

.container__padding__container__go svg{
    margin-right: 5px;
    font-size: 20px;
}

.container__padding__container__text{
    display: flex;
    align-items: center;
}




