

.container{
    position: fixed;
    z-index: 100;
    height: 70px;
    background-color: white;
    top: 0;
    left: 0;
    right: 0;
    padding: 0 6%;
    display: flex;
    transition: top 0.3s ease-in-out;
}

.menubar_container{
    width: 100%;
    height: 100%;
    padding-left: 20px;
    padding-top: 20px;
    box-sizing: border-box;
}


.menubar_container__wrap{
    height: 100%;
    display: flex;
    flex-direction: column;
}
.container.fixed{
    top:0
}

.container.fixed{
    top:-70px
}

.container__wrap__left__div{
    display: flex;
    cursor: pointer;
    text-decoration-line: none;
    color: black;
}

.container__wrap{
    width: 100%;
    margin: 0 auto;
    max-width: 1504px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menubar_container__wrap__center{
    display: flex;
    flex-direction: column;
    margin-bottom: 5vh;

}


.container__wrap__left{
    display: flex;
    margin-right: 30px;
}

.menubar__container__wrap__left{
    margin-bottom: 12vh;
}

.container__wrap__left__logo__img{
    width: 2rem;
}
.container__wrap__left__koboct{
    margin-left: 10px;
    display: flex;
    align-items: center;
}

.container__wrap__left__koboct p{
font-family: 'Lato', sans-serif;
font-size: 16px;
font-weight:900;
}

.container__wrap__center{
width: 100%;
display: flex;
}

.container__wrap__center__div{
    position: relative;
    margin-right: 15px;
    text-decoration-line: none;
    color: black;
    padding: 3px 0;
    font-size: 12px;
    font-weight: 400;
    border-bottom: 1px solid transparent;
    overflow: hidden;
}

.container__wrap__center__div.menubar{
    margin-bottom: 20px;
}


.container__wrap__center__div::after{
    content: "";
    position: absolute;
    right: 100%;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #0078D4;
    transition: left 0.3s ease-in-out;
  }
  
  .container__wrap__center__div:hover::after{
    left: 0;
  }

  .container__wrap__center__div:not(:hover)::after{
    left:100%
  }



.container__wrap__right__language{
    height: 100%;
    display: flex;
    align-items: center;
}

.container__wrap__right__language svg{
    font-size: 23px;
    transition: 1s color ease-in-out;
    cursor:pointer;
}

.container__wrap__right__language svg:hover{
    color:#0078D4
}


.container__wrap__right__menu{
    cursor: pointer;
}

.container__wrap__right__popup{
    z-index: 12;
    width: 15rem;
    top: 2rem;
    right: 0;
    position: absolute;
    margin: 50px;
    border: none;
    border-radius: 10px;
    background-color: white;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 10px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.container__wrap__right__popup__list{
    cursor: pointer;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding:1rem ;
    transition: 0.3s background-color ease-in-out ;
    font-weight: 700;
}

.menubar__container__wrap__right__popup__list{
    cursor: pointer;
    display: flex;
    align-items: center;
    padding:1rem ;
    transition: 0.3s background-color ease-in-out ;
    font-weight: 700;
}

.menubar_container__wrap__center{
    display: flex;
    flex-direction: column;
    margin-bottom: 5vh;
}

.container__wrap__right__popup__list:hover{
background-color: #EAEAEA;
}

.menubar__container__wrap__right__popup__list:hover{
    background-color: #EAEAEA;
    }

.container__wrap__right__popup__list__svg{
    width: 1.8rem;
    margin-right: 15px;
}

.container__wrap__right__popup__list__p_container{
    display: flex;
}

.container__wrap__right__popup__list__p_container__p{
    font-family: 'Lato', sans-serif;
}


@media (max-width: 735px) {
    .container{
        position: fixed;
    }
  }