

.loginbox {
    width: 60%;
    margin: 10px auto;
}

.loginbox__imageUpload{
  width: 100%;
  height: 12rem;
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: center;
}

.loginbox__imageUpload input,
.loginbox__imageUpload textarea
{
  display: none;
}


.loginbox__imageUpload__imgfield{
  width: 150px;
  height: 150px;
}
.loginbox__imageUpload__imgfield label{
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.loginbox__imageUpload__imgfield label svg{
  display: block;
  width: 100%;
  height: 100%;
  color: grey;
  transition: color 0.4s ease-in-out;
}

.loginbox__imageUpload__imgfield label svg:hover{
  color:#1B9BF0;
}

.loginbox__imageUpload__imgfield label img{
  object-fit:cover;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border:0.3px solid grey
}

.loginbox .userbox {
  position: relative;
}

.loginbox .userbox input,
.loginbox .userbox textarea
 {
    width: 97%;
    padding-bottom: 5px;
    padding-top: 26px;
    padding-left: 7px;
    font-size: 16px;
    color: black;
    border-radius: 5px;
    outline: none;
    background: white;
}

.loginbox .userbox textarea{
    height: 150px;
    white-space: pre-wrap;
}



.loginbox .userbox label {
    position: absolute;
    top: -1px;
    left: 0;
    padding: 20px 8px;
    font-size: 16px;
    font-weight: 400;
    color: rgba(0,0,0,.6);
    pointer-events: none;
    transition:0.2s;
    font-weight: 600;
}


.userbox__vaildateMsg{
    padding: 6px;
    padding-left: 10px;
    margin-bottom: 10px;
}

.userbox__vaildateMsg p{
    font-size: 12px;
    font-weight: bold;
}

.loginbox .userbox input:focus ~ label,
.loginbox .userbox input:valid ~ label {
  top: -10px;
  left: 0;
  font-size: 12px;
}

.loginbox .userbox textarea:focus ~ label,
.loginbox .userbox textarea:valid ~ label {
  top: -10px;
  left: 0;
  font-size: 12px;
}




.userbox.error input,
.userbox.error textarea {
    border: 2px solid #FF0000;
  }


.userbox.error p{
    color:#FF0000
}


.userbox.error label{
    color:#FF0000;
    }

.userbox.success input,
.userbox.success textarea  {
    border: 2px solid #1B9BF0;
  }




.userbox.success p{
    color:#1B9BF0;
}
    
    
.userbox.success label{
    color:#1B9BF0;
    }



.userbox.initial input,
.userbox.initial textarea {
    border: 2px solid #dadce0;
  }



.userbox.initial p{
    color:#dadce0
}
    
.userbox.initial label{
        color:#5A6976;
}

.userbox.initial:focus-within label {
    color:#1B9BF0; 
  }

.userbox.initial input:focus,
.userbox.initial textarea:focus 
 {
    border: 2px solid #1B9BF0;
  }

.ContactConfirm__container{
  padding:30px 0;
}

.ContactConfirm__container p{
  font-weight: 500;
  font-size: 15px;
}

.ContactConfirm__container__Loading{
  color:#5A6976
}

.ContactConfirm__container__Success{
  color: #1B9BF0;
}


  @media (max-width: 850px) {
    .loginbox{
      width:100%;
      margin:10px 0px;
    }
  }




