/* ImageSlider.module.css */


.sliderContainer {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    height: 45vw;
  }
  
  .slides {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    user-select: none;
  }
  
  .slide {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    justify-content: center;
    padding: 3px 0;
    position: relative;
    cursor:pointer;
  }

  .slide img{
    height: 42vw;
    object-fit: cover;
  }

  .slide__cover__Up{
    width:36vw;
    position: absolute;
    z-index: 30;
    height: 8vw;
    background-color: white;
    transition:height 0.5s;
  }

  .slide__cover__Down{
    width:36vw;
    position: absolute;
    z-index: 30;
    height: 8vw;
    background-color: white;
    bottom: 0;
    transition:height 0.5s;
  }

  .slide__discription {
    animation: slide-up 0.5s ease-out;
    margin-top: 40px;
    width: 36vw;
  }


  @media (max-width: 1000px){
    .slide img {
        height: 90vw;
    }

    .slide__cover__Up{
        height: 18vw;
        width:70vw;
    }

    .slide__cover__Down{
        height: 18vw;
        width:70vw;
    }

    .slide__discription{
        width:70vw;
        margin-top: 23px;
    }
    }

    

    @keyframes slide-up {
        0% {
          transform: translateY(100%);
          opacity: 0;
        }
        100% {
          transform: translateY(0);
          opacity: 1;
        }
      }
      
   