
.footer {
    text-align: left;
    color: black;
}

.footerUp{
    background-color: #f8f9fa;
    padding: 50px;
}

.footerContainer {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
}


@media (max-width: 800px) {
    .footerContainer {
      flex-direction: column;
    }
  }
  
.footerBottom {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-size: 10px;
    color: #6c757d;
}


.footerBottom__left{
    display: flex;
}

.footerInfo, .footerContact {
    flex: 1;
}

.footerInfo h2, .footerContact h2 {
    font-size: 13px;
    margin-bottom: 10px;
}

.footerInfo p, .footerContact p, .footerContact a {
    font-size: 10px;
    color: #6c757d;
    line-height: 2.5;
    margin: 0;
}

.footerContact a {
    text-decoration: none;
}

.footerContact a:hover {
    color: #495057;
}


.footerInfo__logo{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.container__wrap__left__logo__img{
    width: 35px;
    height: 35px;
}