/* ImageSlider.module.css */


.sliderContainer {
    position: relative;
    height: 600px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  
  .slides {
    display: flex;
    transition: transform 1s ease;
    height: 400px;
    width: 100%;
    margin-bottom: 50px;
  }
  
  .slide {
    min-width: 100%;
    display: flex;
    transition: transform 2s ease;
    height: 100%;
    overflow: hidden;
    justify-content: center;
  }

  .slide img{
    height: 100%;
    object-fit: cover;
  }
  
  .controls {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1257px;
    padding-bottom: 48px;
    padding-left: 28px;
    padding-right: 28px;
    width: 100%;
    border-top: 2px solid black;
  }
  
  .button {
    top: 0;
    transform: translate(0%, -50%);
    width: 12px; /* 버튼의 너비 */
    height: 12px; /* 버튼의 높이 */
    border-radius: 50%; /* 원형 모양을 만들기 위해 */
    margin: 0 5px; /* 버튼 간의 여백 */
    padding: 0; /* 패딩 제거 */
    position: relative;
    border:none;
    background-color: black; /* 버튼 색상 설정 */
    cursor: pointer; /* 마우스 오버 시 커서 변경 */
    display: flex; /* Flexbox를 사용하여 내용 가운데 정렬 */
    align-items: center; /* 세로 방향 가운데 정렬 */
    justify-content: center; /* 가로 방향 가운데 정렬 */
    transition: backgroundColor 1s ease-in-out, left 0.5s ease-in-out;
  }

  .button.buttonColor{
    background-color:#0078D4
  }

  .slidesBefore{
    width: 30px;
    height: 400px;
    background-color: white;
    position: absolute;
    z-index: 5;
  }

  .slidesBefore svg{
    width: 100%;
    height: 250px;
    color: #808080;
    transform: rotate(180deg);
    cursor: pointer;
    transition: color 0.5s ease-in-out;
  }

  .slidesBefore svg:hover{
    color: #0078D4;
  }

  .slidesAfter{
    width: 30px;
    height: 400px;
    background-color: white;
    position: absolute;
    right: 0;
    z-index: 5;
  }

  .slidesAfter svg{
    width: 100%;
    height: 250px;
    color: #808080;
    cursor: pointer;
    transition: color 0.5s ease-in-out;
  }

  .slidesAfter svg:hover{
    color: #0078D4;
  }
  

  .slide__img__Container{
    min-width: 280px;
    min-height: 360px;
    box-sizing: border-box;
    padding-bottom: 48px;
    padding-left: 12px;
    padding-right: 12px;
  }

  .slide__img__Container img{
    width: 250px;
    height: 250px;
  }


  .timeLine{
    width: 100%;
    height: 30px;
  }

  .timeLine{
    display: flex;
    position: relative;
    margin-top: 12px;
  }

  .controls__dots__container{
    width: 100%;
    display: flex;
    position: relative;
  }

  .timeLine__yearsContainer{
    cursor: pointer;
    position: absolute;
  }