.container{
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 500;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container__PopUpPage{
    border-radius: 15px;
    background-color: white;
    height: 700px;
    padding: 0 20px;
    width: 600px;
    overflow-y: scroll;
    position: absolute;
    z-index: 999;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.CoworkersPopUp{
  height: 85%;
  width: 600px;
}


.container__PopUpPage__header__IoClose svg{
    font-size: 25px;
    cursor: pointer;
}


.container__PopUpPage__body{
padding-bottom: 10px;
}

.container__PopUpPage__body__img{
    height: 400px;

}

.container__PopUpPage__body__img.CoworkersPopUp{
  height: 600px;
  width: 100%;
}

.container__PopUpPage__body__img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.container__PopUpPage__background{
    background-color: RGB(183, 193, 200,0.3);
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.container__PopUpPage__CoworkersPopUp__paraGraph{
  margin: 20px 0;
}

.container__PopUpPage__CoworkersPopUp__contact{
  margin-bottom: 10px;
}

.container__PopUpPage__CoworkersPopUp__contact a{
  color: black;
  font-weight: 500;
  list-style: none;
}

  .container__PopUpPage__header{
    text-align: center;
    width: 600px;
    padding: 5px 0;
    background-color: white;
    position: fixed;
  }


  .container__PopUpPage__header p {
    max-width: 850px;
    padding: 10px;
    font-size: 25px;    
    margin-bottom: 10px;
  }


  .container__PopUpPage__NewsRoomCardPopUp{
    
    padding: 12px 18px; /* 상하 12px, 좌우 18px의 패딩을 적용 */
    margin: 16px 0; /* 위아래로 16px의 마진을 적용, 좌우 마진은 없음 */
    background-color: #ffffff; /* 밝은 배경색 설정 */
    border-left: 3px solid #007bff; /* 왼쪽에 3px의 보더를 적용하여 강조 효과 */
    color: #4a4a4a; /* 어두운 회색 글자색 설정 */
    font-size: 0.95rem; /* 조금 더 작은 글자 크기 */
    line-height: 1.6; /* 조금 더 넓은 줄 간격 */
    border-radius: 2px; /* 더 작은 모서리 둥글기 */
    box-shadow: none; /* 박스 그림자 제거 */
  }
  
  .container__PopUpPage__NewsRoomCardPopUp:hover {
    background-color: #f2f2f2; /* 호버 시 약간 어두운 배경색으로 변경 */
  }
  

  .container__PopUpPage__container{
    width: 100%;
    height: 100%;
    position: relative;
  }


@media (max-width: 850px) {
    .container__PopUpPage{
      width:500px;
    }

    .container__PopUpPage__header{
        width: 500px;
    }
    
  }

  @media (max-width: 600px) {
    .container__PopUpPage{
      width:400px;
      height: 75%;
    }

    .container__PopUpPage__header{
        width: 400px;
    }
  }

  @media (max-width: 450px) {
    .container__PopUpPage{
      width:300px;
      height: 85%;
    }

    .container__PopUpPage__header{
        width: 300px;
    }
  }