.container{
    display: flex;
    min-height: 225px;
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 500px;
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
    overflow: hidden;
}

.container.reverse{
    flex-direction: row-reverse;
}


.container.hoverTransition:hover{
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
    transform: translateY(-5px);
}



.container__img_container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container__img_container__img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}



.container__padding__container{
    width: 100%;
    position: relative;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: start;
}

.container__padding__container__fontContainer{
    padding: 0 50px;
}


.container__padding__container__title{
    margin-bottom: 12px;
}

.container__padding__container__title p{
    font-size: 35px;
    font-weight: 500;
}

.container__padding__container__paragraph{
    color: #72818C;
    margin-bottom: 24px;
    line-height: 1.5;
    font-size: 15px;
}


.container__padding__container__yearDate{
    color:black;
    margin-bottom: 24px;
}

.container__padding__container__go{
    display: flex;
    width: 100%;
    cursor: pointer;
    padding: 5px 0px;
}

.container__padding__container__go svg{
    margin-right: 5px;
    font-size: 20px;
}

.container__padding__container__text{
    display: flex;
    align-items: center;
}




